<template>
  <div class="panel-body">
    <button v-if="this.isShow" class="btn btn-success" v-on:click="finish">关闭</button>
    <button v-else class="btn btn-success" v-on:click="start">添加</button>
    <edit-book v-show="this.isShow" :book="this.book" v-on:closeModal="finish"></edit-book>
  </div>
</template>

<script>
  import EditBook from "./EditBook";

  export default {
    components: {
      EditBook
    },
    name: "add-book",
    data: function () {
      return {
        book: {
          name: "",
          url: "",
          limit: ""
        },
        isShow: false
      }
    },
    methods: {
      finish: function () {
        this.isShow = false;
        this.$emit("updateBook")
      },
      start: function () {
        this.isShow = true;
      }
    }
  }
</script>

<style scoped>

</style>



