<template>
  <div class="panel panel-default">
    <div class="panel-heading">邮箱设置</div>
    <div class="panel-body">
      <form class="form-horizontal">

        <div class="form-group">
          <label for="inputEmail3" class="col-sm-2 control-label">收件Kindle Email</label>
          <div class="col-sm-10">
            <input type="email" class="form-control" id="inputEmail3" placeholder="kindle邮箱"
                   v-model="mail.receiver">
          </div>
        </div>

        <div class="form-group">
          <label for="send_email" class="col-sm-2 control-label">发件Email</label>
          <div class="col-sm-10">
            <input type="email" class="form-control" id="send_email" placeholder="你的发件地址"
                   v-model="mail.hostconf.sender">
          </div>
        </div>

        <div class="form-group">
          <label for="pd" class="col-sm-2 control-label">Password</label>
          <div class="col-sm-10">
            <input type="password" class="form-control" id="pd" placeholder="Password" v-model="mail.hostconf.password">
          </div>
        </div>


        <!--测试smtp是否成功-->
        <div class="form-group">
          <label for="test_email" class="col-sm-2 control-label">测试用的邮箱地址</label>
          <div class="col-sm-10">
            <input type="email" class="form-control" id="test_email"
                   placeholder="测试用的邮箱地址" v-model="mail.testReceiver">
          </div>
        </div>

        <div class="form-group">
          <div class="col-sm-offset-2 col-sm-10">
            <button type="submit" class="btn btn-default" v-on:click="updateMailConfig">保存</button>
            <a :href="'/api/mail/test/'+mail.testReceiver">发送测试邮件</a>
          </div>
        </div>

      </form>
    </div>

  </div>

</template>

<script>
  import axios from "axios";

  export default {
    name: "mail-panel",
    data() {
      return {
        mail: null
      }
    },
    created() {
      this.refreshData()
    },
    methods: {
      refreshData: function () {
        axios.get("/api/mail").then(res => (
          this.mail = res.data
        )).catch(function (e) {
          console.log(e)
        });
      },
      updateMailConfig: function (e) {
        e.preventDefault();
        console.log(this.mail);
        var instance = axios.create();
        instance.defaults.headers.post['Content-Type'] = 'application/json';

        instance.post("/api/mail/update", this.mail)
          .then(res => (
            console.log(res.data)
          )).catch(function (e) {
          console.log(e)
        });
      }
    }
  }
</script>

<style scoped>

</style>



