<template>
  <div id="app" class="container">

    <nav-bar/>
    <book-list/>

    <div class="panel panel-default">
      <div class="panel-body">
        <button class="btn btn-info">立即发送</button>
      </div>
    </div>

    <log-panel/>

    <mail-panel/>

    <data-graph/>

    <plain-footer/>
  </div>
</template>

<script>
  import BookList from './components/book/BookList'
  import NavBar from "./components/common/NavBar";
  import PlainFooter from "./components/common/PlainFooter";
  import LogPanel from "./components/log/LogPanel";
  import MailPanel from "./components/config/MailForm";
  import DataGraph from "./components/common/DataGraph";

  export default {
    name: 'app',
    components: {
      DataGraph,
      MailPanel,
      LogPanel,
      PlainFooter,
      NavBar,
      BookList
    }
  }
</script>

