<template>
  <!-- Modal -->
  <div class="modal" v-bind:class="[isFade? 'show':'fade']" id="myModal" tabindex="-1" role="dialog">
    <div class="modal-dialog" role="document">
      <div class="modal-content">

        <div class="modal-header">
          <button type="button" class="close" data-dismiss="modal" aria-label="Close" v-on:click="closeModal"><span
            aria-hidden="true">&times;</span></button>
          <h4 class="modal-title" id="myModalLabel">{{modalTitle}}</h4>
        </div>

        <div class="modal-body">
          <slot>
            内容加载失败...
          </slot>
        </div>

        <slot name="footer"></slot>

      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "basic-small-modal",
    props: ['book', 'isFade', 'modalTitle'],
    methods: {
      closeModal: function () {
        this.$emit('closeModal')
      }
    }
  }
</script>

<style scoped>

</style>



