<template>
  <div class="panel panel-default">
    <!-- Default panel contents -->
    <div class="panel-heading">内容管理</div>
    <!-- Table -->
    <table class="table table-hover">
      <thead>
      <tr>
        <td>书名</td>
        <td>目录链接</td>
        <td>已推送至</td>
        <td>推送频率</td>
        <td>操作</td>
      </tr>
      </thead>
      <tbody>
      <tr v-for="bc in books">
        <td>{{bc.name}}</td>
        <td><a :href="bc.url">{{bc.url}}</a></td>
        <td>{{bc.read_at}}</td>
        <td>{{bc.limit}}章</td>
        <td>
          <a class="btn btn-default" v-on:click="showModal(bc)" role="button">编辑</a>
          <a class="btn btn-default" v-on:click="showDelete(bc)" role="button">删除</a>
        </td>
      </tr>
      </tbody>
    </table>

    <add-book v-on:updateBook="refreshBook"></add-book>

    <modal :isFade="isFade" :modalTitle="modalTitle" v-on:closeModal="closeModal">
      <edit-book :book="modalBook" :type="update" v-on:updateBook="refreshBook" v-on:closeModal="closeModal"></edit-book>
    </modal>

    <modal :isFade="isHideDeleteCheck" :modalTitle="'请确认操作'" v-on:closeModal="isHideDeleteCheck=false">
      <p>你确定要删除{{this.bookToDelete.name}}么?</p>
      <div slot="footer" class="modal-footer">
        <button type="button" class="btn btn-default" data-dismiss="modal" v-on:click="isHideDeleteCheck=false">关闭
        </button>
        <button type="button" class="btn btn-default" v-on:click="deleteBook">确认删除</button>
      </div>
    </modal>

  </div>
</template>

<script>
  import axios from "axios";
  import modal from "./BasicSmalModal";
  import EditBook from "./EditBook";
  import AddBook from "./AddBook";

  export default {
    components: {
      AddBook,
      EditBook,
      modal
    },
    name: 'book-list',
    data() {
      return {
        books: [],
        modalBook: null,
        isFade: false,
        modalTitle: "编辑书籍信息",
        isHideDeleteCheck: false,
        bookToDelete: {
          id: -1,
          name: "",
          url: "",
          limit: 0
        }
      }
    },
    created() {
      this.refreshBook()
    },
    watch: {
      books: function () {
        this.modalBook = this.books[0]
      }
    },
    methods: {
      showModal: function (book) {
        this.modalBook = JSON.parse(JSON.stringify(book));
        this.isFade = true;
      }
      ,
      closeModal: function () {
        this.isFade = false;
      },
      refreshBook: function () {
        axios.get('/api/book/all').then(response =>
          (
            this.books = response.data
          )
        )
      },
      deleteBook: function () {
        axios.get('/api/book/delete/' + this.bookToDelete.id).then(res => (
          console.log("success")
        )).catch(e => (
          console.log(e)
        ));
        this.isHideDeleteCheck = false;
        this.refreshBook()
      },
      showDelete: function (bk) {
        this.bookToDelete = bk;
        this.isHideDeleteCheck = true
      }
    }

  }
</script>




