<template>
  <form class="form-horizontal">

    <div class="form-group">
      <label for="bookName" class="col-sm-2 control-label">书名</label>
      <div class="col-sm-10">
        <input type="text" class="form-control" id="bookName" placeholder="书名" name="book" v-model="book.name"
        >
      </div>
    </div>

    <div class="form-group">
      <label for="url" class="col-sm-2 control-label">目录页地址</label>
      <div class="col-sm-10">
        <input type="url" class="form-control" id="url" placeholder="例如http://www.book.com" name="url"
               v-model="book.url">
      </div>
    </div>

    <div class="form-group">
      <label for="limit" class="col-sm-2 control-label">发送章节数</label>
      <div class="col-sm-10">
        <input type="text" class="form-control" id="limit" placeholder="例如3" name="limit" v-model="book.limit">
      </div>
    </div>

    <div class="form-group">
      <div class="col-sm-offset-2 col-sm-10">
        <button class="btn btn-default" type="submit" v-on:click="save">保存</button>
      </div>
    </div>

  </form>
</template>

<script>
  import axios from "axios";

  export default {
    name: "edit-book",
    props: ["book", "type"],
    methods: {
      save: function (e) {
        e.preventDefault();
        if ("update" != null && this.type == "update") {
          this.updateBook();
        } else {
          this.addBook();
        }
      },
      addBook: function () {
        axios.post('/api/book/add', this.book).then(res => {
          console.log(res.data)
        }).catch(exception => (
          alert("保存失败")
        ));
        this.$emit("closeModal");
        this.$emit("updateBook");
      },
      updateBook: function () {
        axios.post('/api/book/update/' + this.book.id, this.book).then(res => {
          console.log(res.data)
        }).catch(exception => (
          alert("保存失败")
        ));
        this.$emit("closeModal");
        this.$emit("updateBook");
      }
    }
  }
</script>

<style scoped>

</style>



